    padding-bottom: .5rem;
    padding-top: .5rem
}

.breadcrumbs>ol,.breadcrumbs>ul {
    align-items: center;
    display: flex;
    min-height: -webkit-min-content;
    min-height: min-content;
    white-space: nowrap
}

.breadcrumbs>ol>li,.breadcrumbs>ul>li {
    align-items: center;
    display: flex
}

.breadcrumbs>ol>li>a,.breadcrumbs>ul>li>a {
    align-items: center;
    cursor: pointer;
    display: flex
}

@media (hover: hover) {
    .breadcrumbs>ol>li>a:hover,.breadcrumbs>ul>li>a:hover {
        text-decoration-line:underline
    }

    .label a:hover {
        --tw-text-opacity: 1;
        color: hsl(215 28% 17%/var(--tw-text-opacity));
        color: hsl(var(--bc)/var(--tw-text-opacity))
    }

    :where(.menu li:not(.menu-title):not(.disabled)>:not(ul):not(details):not(.menu-title)):where(.active),:where(.menu li:not(.menu-title):not(.disabled)>details>summary:not(.menu-title)):where(.active) {
