    }
}

.btn {
    --tw-border-opacity: 1;
    --tw-bg-opacity: 1;
    --tw-text-opacity: 1;
    align-items: center;
    -webkit-animation: button-pop .25s ease-out;
    animation: button-pop .25s ease-out;
    -webkit-animation: button-pop var(--animation-btn,.25s) ease-out;
    animation: button-pop var(--animation-btn,.25s) ease-out;
    background-color: hsl(0 0% 95%/var(--tw-bg-opacity));
    background-color: hsl(var(--b2)/var(--tw-bg-opacity));
    border-color: transparent;
    border-color: hsl(var(--b2)/var(--tw-border-opacity));
    border-radius: .5rem;
    border-radius: var(--rounded-btn,.5rem);
    border-width: 1px;
    border-width: var(--border-btn,1px);
    cursor: pointer;
    display: inline-flex;
    flex-shrink: 0;
    flex-wrap: wrap;
    font-size: .875rem;
    font-weight: 600;
    gap: .5rem;
    height: 3rem;
    justify-content: center;
    line-height: 1.25rem;
    line-height: 1em;
    min-height: 3rem;
    outline-color: #1f2937;
    outline-color: hsl(var(--bc)/1);
    padding-left: 1rem;
    padding-right: 1rem;
    text-align: center;
    text-decoration-line: none;
    text-transform: uppercase;
    text-transform: var(--btn-text-case,uppercase);
    transition-duration: .2s;
    transition-property: color,background-color,border-color,text-decoration-color,fill,stroke,opacity,box-shadow,-webkit-transform,-webkit-filter,-webkit-backdrop-filter;
    transition-property: color,background-color,border-color,text-decoration-color,fill,stroke,opacity,box-shadow,transform,filter,backdrop-filter;
    transition-property: color,background-color,border-color,text-decoration-color,fill,stroke,opacity,box-shadow,transform,filter,backdrop-filter,-webkit-transform,-webkit-filter,-webkit-backdrop-filter;
    transition-timing-function: cubic-bezier(.4,0,.2,1);
    -webkit-user-select: none;
    user-select: none
}

.btn,.btn-disabled,.btn:disabled,.btn[disabled] {
    color: hsl(215 28% 17%/var(--tw-text-opacity));
    color: hsl(var(--bc)/var(--tw-text-opacity))
}

.btn-disabled,.btn:disabled,.btn[disabled] {
    --tw-border-opacity: 0;
    --tw-bg-opacity: 0.2;
    --tw-text-opacity: 0.2;
    background-color: hsl(214 20% 21%/var(--tw-bg-opacity));
    background-color: hsl(var(--n)/var(--tw-bg-opacity));
    pointer-events: none
