.btn-circle,.btn-square {
    height: 3rem;
    padding: 0;
    width: 3rem
}

.btn-circle {
    border-radius: 9999px
}

.btn-group>input[type=radio].btn {
    -webkit-appearance: none;
    appearance: none
}

.btn-group>input[type=radio].btn:before {
    content: attr(data-title)
}

.btn:is(input[type=checkbox]),.btn:is(input[type=radio]) {
    -webkit-appearance: none;
    appearance: none
}

.btn:is(input[type=checkbox]):after,.btn:is(input[type=radio]):after {
    --tw-content: attr(aria-label);
    content: var(--tw-content)
}

.card {
    border-radius: 1rem;
    border-radius: var(--rounded-box,1rem);
    display: flex;
    flex-direction: column;
    position: relative
}

.card:focus {
    outline: 2px solid transparent;
    outline-offset: 2px
}

.card-body {
    display: flex;
    flex: 1 1 auto;
    flex-direction: column;
    gap: .5rem;
    padding: 2rem;
    padding: var(--padding-card,2rem)
}

.card-body :where(p) {
    flex-grow: 1
}

.card-actions {
    align-items: flex-start;
    display: flex;
    flex-wrap: wrap;
    gap: .5rem
}

.card figure {
    align-items: center;
    display: flex;
    justify-content: center
}

.card.image-full {
    display: grid
}

.card.image-full:before {
    --tw-bg-opacity: 1;
    background-color: hsl(214 20% 21%/var(--tw-bg-opacity));
    background-color: hsl(var(--n)/var(--tw-bg-opacity));
    border-radius: 1rem;
    border-radius: var(--rounded-box,1rem);
    content: "";
    opacity: .75;
    position: relative;
    z-index: 10
}

.card.image-full:before,.card.image-full>* {
    grid-column-start: 1;
    grid-row-start: 1
}

.card.image-full>figure img {
    height: 100%;
    object-fit: cover
}

.card.image-full>.card-body {
    --tw-text-opacity: 1;
    color: hsl(212 19% 87%/var(--tw-text-opacity));
    color: hsl(var(--nc)/var(--tw-text-opacity));
    position: relative;
    z-index: 20
}

.checkbox {
    --chkbg: var(--bc);
    --chkfg: var(--b1);
    --tw-border-opacity: 0.2;
    -webkit-appearance: none;
    appearance: none;
    border-color: hsl(var(--bc)/var(--tw-border-opacity));
    border-radius: .5rem;
    border-radius: var(--rounded-btn,.5rem);
    border-width: 1px;
    cursor: pointer;
    flex-shrink: 0;
    height: 1.5rem;
    width: 1.5rem
}

